.follow {
  font-family: 'Open Sans', sans-serif;
  text-align: left;
  max-width: 425px;
  margin: 10px auto 0 auto;
  display: flex;
  flex-direction: column;
}

.follow .title {
  color: gray;
  font-size: 16px;
  margin: 15px 0 10px 10px;
}

.follow .steps {
  margin: 5px 0 0 15px;
}

.follow
  .ant-steps-item-finish
  > .ant-steps-item-container
  > .ant-steps-item-tail::after,
.follow
  .ant-steps-item-finish
  .ant-steps-item-icon
  > .ant-steps-icon
  .ant-steps-icon-dot,
.follow
  .ant-steps-item-process
  .ant-steps-item-icon
  > .ant-steps-icon
  .ant-steps-icon-dot {
  background-color: #fc885c;
}

.ant-steps-item-process .ant-steps-item-icon {
  border-color: #fc885c;
}

.follow .order {
  margin: 0;
}

.followBtn {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.followBtn .submitBtn {
  margin: 30px auto 0 auto;
  width: 200px;
  color: white;
  background-color: #fc885c;
  border-color: #fc885c;
  border-radius: 20px;
}

.followBtn .linkBtn {
  width: 200px;
  margin: 10px auto 20px auto;
  color: #fc885c;
  border: 0;
  border-radius: 20px;
}
