.menu {
  text-align: left;
  max-width: 425px;
  margin: 0 auto 0 auto;
  display: flex;
  flex-direction: column;
}

.menu .title {
  color: gray;
  font-size: 16px;
  margin: 15px 0 5px 10px;
}

.menu .linkBtn {
  width: 100%;
  margin: 0 auto 0 auto;
}

.menu .productContainer {
  height: 60px;
  margin: 5px 5%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-content: flex-start;
}

.menu .productContainer img {
  height: auto;
  margin: auto 3% auto 0;
  width: 17%;
}

.menu .productContainer h3 {
  font-family: 'Open Sans', sans-serif;
  font-weight: 500;
  font-size: 13px;
  min-width: 69%;
  margin: auto auto 3px 0;
  color: #535355;
}

.menu .productContainer h6 {
  font-family: 'Open Sans', sans-serif;
  font-size: 12px;
  font-weight: 500;
  margin: 0 auto auto 0;
  color: #9b9b9b;
  font-weight: lighter;
}

.menu .productContainer .addBtn {
  margin: auto 0 auto auto;
  height: 40px;
  width: 40px;
}

.menu .alert {
  width: inherit;
  color: #fc885c;
  font-family: 'Open Sans', sans-serif;
  font-weight: 500;
  font-size: 13px;
  padding: 0;
  position: absolute;
  left: 0;
  right: 0;
  margin: 10px auto 0 auto;
  text-align: center;
}

.menu .submitBtn {
  margin: 30px auto 0 auto;
  width: 200px;
  color: white;
  background-color: #fc885c;
  border-color: #fc885c;
  border-radius: 20px;
}

.menu .linkBtn {
  width: 200px;
  margin: 10px auto 20px auto;
  color: #fc885c;
  border: 0;
  border-radius: 20px;
}
