.order {
  text-align: left;
  max-width: 425px;
  margin: 10px auto 0 auto;
  display: flex;
  flex-direction: column;
}

.order .title {
  color: gray;
  font-size: 16px;
  margin: 15px 0 10px 10px;
}

.order .linkBtn {
  width: 100%;
  margin: 0 auto 0 auto;
}

.order .productContainer {
  height: 60px;
  margin: 10px 5%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-content: flex-start;
}

.order .productContainer img {
  height: auto;
  margin: auto 3% auto 0;
  width: 17%;
}

.order .productContainer h3 {
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
  font-size: 14px;
  min-width: 69%;
  margin: auto auto 3px 0;
  color: #535355;
}

.order .productContainer h6 {
  font-family: 'Open Sans', sans-serif;
  font-size: 12px;
  font-weight: 500;
  margin: 0 auto auto 0;
  color: #9b9b9b;
  font-weight: lighter;
}

.order .productContainer .addBtn {
  margin: auto 0 auto auto;
  height: 40px;
  width: 40px;
}

.order .totalPrice {
  margin: 0 0 20px 10px;
}

.order .totalPrice span {
  font-weight: bold;
}

.order .selectPayment > .ant-select-arrow > span {
  font-family: 'Open Sans', sans-serif;
  font-weight: bolder;
  color: #fc885c;
}

.order .selectPayment > .ant-select-arrow > span {
  font-family: 'Open Sans', sans-serif;
  font-weight: bolder;
  color: #fc885c;
}

.ant-select-item-option-content {
  text-align: left;
}

.orderBtns {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.orderBtns .submitBtn {
  margin: 30px auto 0 auto;
  width: 200px;
  color: white;
  background-color: #fc885c;
  border-color: #fc885c;
  border-radius: 20px;
}

.orderBtns .linkBtn {
  width: 200px;
  margin: 10px auto 20px auto;
  color: #fc885c;
  border: 0;
  border-radius: 20px;
}
