.confirmed {
  font-family: 'Open Sans', sans-serif;
  text-align: left;
  max-width: 425px;
  margin: 10px auto 0 auto;
  display: flex;
  flex-direction: column;
}

.confirmed .title {
  font-size: 25px;
  font-weight: bolder;
  text-align: center;
}

.confirmed .msg {
  font-size: 15px;
  font-weight: 500;
  text-align: center;
  max-width: 80%;
  margin: 0 auto;
}

.confirmed .submitBtn {
  margin: 30px auto 0 auto;
  width: 200px;
  color: white;
  background-color: #fc885c;
  border-color: #fc885c;
  border-radius: 20px;
}

.confirmed .linkBtn {
  width: 200px;
  margin: 10px auto 20px auto;
  color: #fc885c;
  border: 0;
  border-radius: 20px;
}
